.striped {
  background-image: linear-gradient(
    45deg,
    #828282 38.46%,
    #bababa 38.46%,
    #bababa 50%,
    #828282 50%,
    #828282 88.46%,
    #bababa 88.46%,
    #bababa 100%
  );
  background-size: 18.38px 18.38px;

  outline: solid rgba(0, 0, 0, 0.3) 2px;
  outline-offset: -1px;

  opacity: 0.7;
}
